/* ---base rules--- */
button,
input,
select,
a {
  font-family: "Nunito Sans", sans-serif;
  color: inherit;
}

a {
  text-decoration: none;
}

div svg {
  max-inline-size: 100%;
}
div img {
  max-inline-size: 100%;
}

ul {
  list-style-type: none;
  padding-left: 0;
}

/* ---layout--- */
.header,
.main {
  padding: 0 5vw;
}

/* ---modules---- */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__theme__switch {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 110px;
}
.header__theme__icon {
  max-width: 20px;
}

.countriesSearchPage section {
  margin-top: 30px;
}

.countriesSearchPage .search {
  margin-top: 6px;
}

.search__icon {
  height: 24px;
  position: relative;
  top: 38px;
  left: 28px;
}
.search__input {
  box-sizing: border-box;
  width: 100%;
  height: 45px;
  padding: 0 10px 0 80px;
  border: none;
  border-radius: 5px;
  font-size: inherit;
}

select {
  width: 200px;
  height: 45px;
  padding: 0 16px;
  border: none;
  border-radius: 5px;
  font-size: inherit;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  background-image: url("data:image/svg+xml;utf8,<svg fill='white' height='24' viewBox='0 0 24 24' width='24' xmlns='http://www.w3.org/2000/svg'><path d='M7 10l5 5 5-5z'/><path d='M0 0h24v24H0z' fill='none'/></svg>");
  background-repeat: no-repeat;
  background-position: right 10px top 10px;
}

.countryDataLabel {
  font-weight: 800;
  font-size: 1.05em;
}

.countriesList {
  display: grid;
  max-width: 1380px;
  margin: auto;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  justify-items: center;
  gap: 60px;
}
.countriesList__countryGeogrpahy {
  padding: 20px;
  line-height: 1.8;
}
.countriesList__countryGeogrpahy li:first-child {
  padding-bottom: 10px;
  font-size: 1.4em;
}

.countryItem {
  border-radius: 5px;
}
.countryItem img {
  border-radius: 5px 5px 0 0;
}

.countryPage__backToSearch {
  display: inline-block;
  padding: 5px 10px;
  margin: 30px 0;
  border-radius: 5px;
}

.countryPage {
  line-height: 2.4;
}
.countryPage ul {
  margin-bottom: 40px;
}
.countryPage__name {
  font-weight: 800;
  font-size: 2em;
}
.countryPage__bordersLabel {
  font-weight: 800;
  font-size: 1.05em;
}

.countryPage_bordersList {
  display: flex;
  flex-flow: row wrap;
}
.countryPage_bordersList li {
  padding: 0 20px;
  border-radius: 5px;
  margin: 10px;
}

/* desktop */
@media (min-width: 1440px) {
  .header,
.main {
    padding: 0 8vw;
  }
  .countriesSearchPage {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: 30px 0;
    gap: 30px 0;
  }
  .countriesSearchPage .search {
    justify-self: start;
    width: 500px;
    margin-top: 0;
  }
  .countriesSearchPage .regionMenu {
    justify-self: end;
  }
  .countriesSearchPage section:last-child {
    grid-column: 1/3;
  }
  .countriesSearchPage .countriesList {
    max-width: none;
  }
}
/* ---theme--- */
html {
  background-color: hsl(207deg, 26%, 17%);
  color: white;
}

select,
input,
button,
.countryItem {
  background-color: hsl(209deg, 23%, 22%);
}

.header {
  background-color: hsl(209deg, 23%, 22%);
}
.header__theme__switch {
  border: none;
}
.header__theme__icon {
  fill: white;
}

.search__icon {
  fill: white;
}

.countryPage__backToSearch,
.countryPage_bordersList li {
  background-color: hsl(209deg, 23%, 22%);
}

